  		var vm=this;
      vm.msg="";
      var j={
        "id":"123",
        "password":"123"
      }
      console.log(vm.loginModel)
        //if(!this.isloginEmpty()){
      vm.$http.post(vm.loginUrl,
                  vm.loginModel,
                   {
